import { Button, Modal } from "antd";
import Layout, { Content } from "antd/lib/layout/layout";
import List from "antd/lib/list";
import Title from "antd/lib/typography/Title";
import { useState } from "react";
import { useRecoilValue } from "recoil";
import DIYIcon from "../component/DIYIcon.component";
import MultipleChoice from "../component/multipleChoice.component";
import { data, passScore, requiredCountOfSelected, examTitle, examDescription } from "../constants/common";
import { IMultipleChoiceProps } from "../interface/index";
import { selectedCountState, selectedTotalScoreState } from "../recoil/selectors/examFormSelectors";
import { usernameState } from "../recoil/selectors/infoFormSelectors";

const Examination = () => {
  const selectedLength = useRecoilValue(selectedCountState)
  const selectedTotalScore = useRecoilValue(selectedTotalScoreState)
  const username = useRecoilValue(usernameState)
  const [isModalVisible, setIsModalVisible] = useState(false);
  const ModalTitle = selectedTotalScore < passScore ? `很遗憾，基础知识测验未通过，请再接再厉。` : `${username}，恭喜您通过了基础知识测验。`;
  const IconName = selectedTotalScore < passScore ? "icon-crying2" : "icon-success";

  const handleSubmit = () => {
    if (localStorage.getItem('score')) {
      alert('请勿重复提交');
      return;
    }
    localStorage.setItem('score', selectedTotalScore.toString());
    setIsModalVisible(true);
  }

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <Layout className="flex flex-col justify-center w-600 items-center padding-0-70 pb-10 bg-gradient-to-b from-cyan-100 to-blue-300">
      <Content className="bg-white px-16 mb-5 pt-10 pb-10 mt-10 rounded-lg">
        <Title level={2} className="text-3xl text-center text-blue-400">{examTitle}</Title>
        <Title level={4} className="py-3 text-red-400">{examDescription}</Title>
        <List split={false}>
          {data.map((item: IMultipleChoiceProps, index: number) => {
            return (
              <MultipleChoice index={index} key={item.id} {...{ ...item }}></MultipleChoice>
            )
          })}
        </List>
        <Button
          className="block mx-auto"
          type="primary"
          onClick={handleSubmit}
          disabled={selectedLength < requiredCountOfSelected ? true : false}
        >
          提交试卷
        </Button>
        <Modal
          title={ModalTitle}
          visible={isModalVisible}
          centered={true}
          footer={null}
          onCancel={handleCancel}
          style={{ textAlign: "center" }}
        >
          <div className="p-10 w-600 flex justify-center">
            <DIYIcon type={IconName} scriptUrl='//at.alicdn.com/t/font_3367817_zwt63hsj7ya.js' />
          </div>
        </Modal>
      </Content>
    </Layout>
  )
};

export default Examination;