import { selector } from "recoil";
import { multiChoiceSelectedState } from "../atoms/examFormAtoms";

export const selectedCountState = selector({
  key: 'selectedCountState',
  get: ({get}) => {
    const selected = get(multiChoiceSelectedState);
    const selectedCount = selected.length;
    return selectedCount;
  },
});

export const selectedTotalScoreState = selector({
  key: 'selectedTotalScoreState',
  get: ({get}) => {
    const selected = get(multiChoiceSelectedState);
    const totalScore = selected.reduce((prev, cur) => cur.selected === cur.answer ? prev+10 : prev, 0);
    return totalScore;
  },
});