import { Form, Input, Button, Typography } from "antd";
import { IchangedValuesType, IUserInfos, ItemsTypeArray } from "../interface";
interface IFormInformationProps {
  onFinish: (values: IUserInfos) => void;
  onFinishFailed: (errorInfo: any) => void;
  labelCol: number;
  wrapperCol: number;
  onValuesChange: (changedValues: IchangedValuesType, allValues: IUserInfos) => void;
  disable: boolean;
  formValidation: ItemsTypeArray[];
}

export default function FormInformation(props: IFormInformationProps) {
  const {
    onFinish,
    onFinishFailed,
    formValidation,
    labelCol,
    wrapperCol,
    onValuesChange,
    disable,
  } = props;
  const { Title } = Typography;
  return (
    <div>
      <Title level={3} className="text-center font-semibold text-2xl pt-8 pb-6">基本信息</Title>
      <Form
        name="basic"
        labelCol={{ span: labelCol }}
        wrapperCol={{ span: wrapperCol }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
        onValuesChange={onValuesChange}
      >
        {formValidation.map((num: ItemsTypeArray, index: number) => {
          return (
            <Form.Item
              key={index}
              label={num.label}
              name={num.name}
              rules={num.rule}
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 19 }}
            >
              <Input
                placeholder={num.placeholder}
                prefix={num.icon.prefix}
                style={{ minWidth: 200 }}
              />
            </Form.Item>
          );
        })}
        <Form.Item wrapperCol={{ span: 24 }} style={{textAlign:'center'}}>
          <Button type="primary" htmlType="submit" disabled={disable}>
            提交
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}
