import { createFromIconfontCN } from "@ant-design/icons";
interface IDIYIconProps  {
  type: string,
  scriptUrl:string
}

export default function DIYIcon(props:IDIYIconProps) {
  const { type, scriptUrl } = props
  const DIYIcon = createFromIconfontCN({
    scriptUrl,
  });

  return (
    <>
      <DIYIcon type={ type } style={{fontSize:'300px'}}/>
    </>
  )
}
